import './App.css';
import Container from "./components/Container"
import React, { useEffect, useState } from "react";
const API = 'https://ionutdaniel99.site/api_v1/Portfolio_Json_Resume';

const App = () => {
  const [resumeData, setResumeData] = useState(null);
  useEffect(() => {
    fetch(API)
      .then((res) => res.json())
      .then((data) => {
        setResumeData(data);
      });
  }, []);

  return (
    <div className="main">
      { resumeData && <Container data={resumeData} />}
    </div>
  );
}

export default App;

